








import { Component, Prop } from 'vue-property-decorator';
import AbstractVueComponent from '../../abstract-vue-component';

const DEFAULT_COLOR = '#999';

interface ColorMap {
  [index: string]: string;
}

const COLOR_MAP: ColorMap = {
  danger: '#f44',
  primary: '#38f',
  success: '#06bf04',
};

@Component({
  name: 'nj-tag',
  components: {},
})
export default class NinjaTag extends AbstractVueComponent {
  @Prop(Boolean) private mark!: boolean;
  @Prop(String) private size?: 'medium' | 'large';
  @Prop(String) private color?: string;
  @Prop(String) private type?: string;
  @Prop(Boolean) private plain?: boolean;

  get style() {
    const color = this.color || COLOR_MAP[this.type!] || DEFAULT_COLOR;
    const key = this.plain ? 'color' : 'backgroundColor';
    return { [key]: color };
  }
}
