




































import { Component, Vue } from 'vue-property-decorator';

const tabRoutePages = ['homePage', 'controlPage', 'servicePage', 'personalPage'];

@Component({
  name: 'main-wrapper',
  components: {},
})
export default class MainWrapper extends Vue {
  private activeTabBar = -1;

  private get activeTabBar() {
    return tabRoutePages.findIndex((item) => {
      return item === this.$route.name;
    });
  }
}
