import Vue from 'vue';
import NinjaUI from './components/ninjaui';
import Vant from 'vant';
import App from './App.vue';
import router from './router/index';
import store from './store';

Vue.config.productionTip = false;

Vue.use(NinjaUI);
Vue.use(Vant);

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
