import { Vue } from 'vue-property-decorator';

/**
 * bem helper
 * bem() // 'button'
 * bem('text') // 'button__text'
 * bem({ disabled }) // 'button button--disabled'
 * bem('text', { disabled }) // 'button__text button__text--disabled'
 * bem(['disabled', 'primary']) // 'button button--disabled button--primary'
 */

const ELEMENT = '__';
const MODS = '--';

const join = (name: string, symbol: string, el?: string): string => {
  return el ? name + symbol + el : name;
};

const prefix = (name: string, mods: object): any => {
  if (typeof mods === 'string') {
    return join(name, mods, MODS);
  }

  if (Array.isArray(mods)) {
    return mods.map((item) => prefix(name, item));
  }

  const ret = {};
  Object.keys(mods).forEach((key) => {
    // @ts-ignore
    ret[name + MODS + key] = mods[key];
  });
  return ret;
};

export default class AbstractVueComponent extends Vue {
  protected bem(el: string | object = '', mods?: object): any {
    console.log(el, mods);
    const { name } = this.$options;
    if (typeof el !== 'string') {
      mods = el;
      el = '';
    }
    el = join(name || '', ELEMENT, el);
    return mods ? [el, prefix(el, mods)] : el;
  }
}
