

















import { Component, Prop } from 'vue-property-decorator';
import AbstractVueComponent from '../../abstract-vue-component';

const DEFAULT_COLOR = '#FFF';
const DEFAULT_BORDER_COLOR = '#1B9AF7';
const DEFAULT_BACKGROUND_COLOR = '#1B9AF7';

@Component({
  name: 'nj-button',
  components: {},
})
export default class NinjaButton extends AbstractVueComponent {
  @Prop(Boolean) private wrap?: boolean;
  @Prop(Boolean) private circle?: boolean;
  @Prop(Boolean) private raised?: boolean;
  // @Prop(String) private color?: string;

  get style() {
    // const color = this.color || DEFAULT_COLOR;
    return {
      ['color']: DEFAULT_COLOR,
      ['borderColor']: DEFAULT_BORDER_COLOR,
      ['backgroundColor']: DEFAULT_BACKGROUND_COLOR,
    };
  }
}
