








import { Component, Prop } from 'vue-property-decorator';
import AbstractVueComponent from '../../abstract-vue-component';

const DEFAULT_COLOR = '#fff';

@Component({
  name: 'nj-card',
  components: {},
})
export default class NinjaCard extends AbstractVueComponent {
  @Prop(Boolean) private shadow?: boolean;
  @Prop(Boolean) private rounded?: boolean;
  @Prop(String) private color?: string;

  get style() {
    const color = this.color || DEFAULT_COLOR;
    return { ['backgroundColor']: color };
  }
}
