import Vue from 'vue';
import Router from 'vue-router';
import SignInPage from '../views/singIn.page.vue';
import SignUpPage from '../views/singUp.page.vue';
import MainWrapper from '../views/commons/main-wrapper.component.vue';
import HomePage from '../views/home.page.vue';
import ControlPage from '../views/control.page.vue';
import ServicePage from '../views/service.page.vue';
import PersonalPage from '../views/personal.page.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/signIn',
      name: 'signInPage',
      component: SignInPage,
    },
    {
      path: '/signUp',
      name: 'signUpPage',
      component: SignUpPage,
    },
    {
      path: '/m',
      component: MainWrapper,
      children: [
        {
          path: 'home',
          name: 'homePage',
          component: HomePage,
        },
        {
          path: 'control',
          name: 'controlPage',
          component: ControlPage,
        },
        {
          path: 'service',
          name: 'servicePage',
          component: ServicePage,
        },
        {
          path: 'personal',
          name: 'personalPage',
          component: PersonalPage,
        },
      ],
    },
  ],
});
